body{
  margin: 0px;padding: 0px;font-family: "Montserrat", Helvetica, Arial, sans-serif;font-size: 12px;
}
.appdiv{
  position: absolute;top: 0px;left: 0px;right: 0px;bottom: 0px;
}

$primary-color:var(--primarycolorval);
$secondary-color:var(--secondarycolorval);
$font-secondary:Helvetica, Arial, sans-serif;

/* Matrix Colors */
$letter-unavailable:var(--letter_unavailable);
$letter-available:var(--letter_available);
$letter-correct:var(--letter_correct);
$matrix-letter-color:var(--matrix_letter_color);

/* Popup Colors */
$popup-background:var(--popup_background);
$settings-row-border:var(--settings_row_border);

/* Theme Colors */
.dark{
  --primarycolorval:#000000;
  --secondarycolorval: #ffffff;
  --letter_unavailable: #999999;
  --letter_available: #b59f3b;
  --letter_correct: #538d4e;
  --matrix_letter_color: #ffffff;
  --popup_background: rgba(0,0,0,0.97);
  --settings_row_border: #666666;
  color: $secondary-color;
  background: $primary-color;
}
.light{
  --primarycolorval:#ffffff;
  --secondarycolorval: #000000;
  --letter_unavailable: #787c7e;
  --letter_available: #c9b458;
  --letter_correct: #6aaa64;
  --matrix_letter_color: #ffffff;
  --popup_background: rgba(255,255,255,0.97);
  --settings_row_border: #666666;
  /*normal border:  d3d6da*/
  /*selected border:  878a8c;*/
  color: $secondary-color;
  background: $primary-color;
}

.header{
  width: 100%;position: relative;margin: 0 auto;height: 42px;box-sizing: border-box;border-bottom: 1px solid $secondary-color;min-width: 320px;
}
.headercontents{
  display: flex;flex-direction: row;justify-content: space-evenly;
}
.headericon_div{
  cursor: pointer;
}
.headericon_div img{
  width: 24px;
}
.dark .headericon_div img{
  filter: invert(1);
}
.apptitle{
  text-align: center;margin: 0px;
}
.containerclass{
  width: 100%;height: calc(100% - 42px);min-width: 320px;max-width: 500px;position: relative;margin: 0 auto;padding: 10px;box-sizing: border-box;
}

/* How To */
.example_word{
  width: 100%;float: left;z-index:1;
}
.example_word_letter{
  float: left;width: 40px;height: 40px;text-align: center;font-size: 22px;font-family: $font-secondary;margin-right: 10px;position: relative;
}
.example_word_letter span{
  position: absolute;transform: translate(-50%, -50%);top: 50%;left: 50%;
}
.example_word_description{
  width: 100%;float: left;margin: 20px 0px;
}

/* Matrix */
.game_div{
  height: 100%;display: flex;flex-direction: column;
}
.matrix_div{
  flex-grow: 3;display: flex;flex-direction: column;max-height: 410px;
}
.matrix_row{
  display: flex;margin-bottom: 0.5%;justify-content: center;flex-grow: 1;max-height: 65px;
}
.matrix_column{
  position: relative;max-width: 60px;margin: 0.5%;font-size: 25px;text-align: center;flex-grow: 1;max-height: 60px;line-height: 60px;font-family: $font-secondary;color: $matrix-letter-color;box-sizing: border-box;
}
.matrix_column span{
  position: absolute;top: 50%;left: 50%;transform: translate(-50%,-50%);
}
.letter_default{
  color: $secondary-color !important;border: 1px solid $settings-row-border;
}
.letter_unavailable{
  background-color: $letter-unavailable;animation: animatecolumn 1s 1 forwards;
}
.letter_available{
  background-color: $letter-available;animation: animatecolumn 1s 1 forwards;
}
.letter_correct{
  background-color: $letter-correct;animation: animatecolumn 1s 1 forwards;
}
@keyframes animatecolumn{
  0%{transform: rotateX(0deg);}
  100%{transform: rotateX(360deg);}
}
@-webkit-keyframes animatecolumn{
  0%{transform: rotateX(0deg);}
  100%{transform: rotateX(360deg);}
}

/* Keyboard */
.keyboard{
  flex-grow: 1;display: flex;flex-direction: column;max-height: 350px;
}
.keyboard_row{
  display: flex;justify-content: center;flex-grow: 1;
}
.keyboardbtn_div {
  flex-grow: 1;margin: 2px;
}
.keyboardbtn{
  width: 100%;height: 100%;padding: 5px;font-weight: bold;cursor: pointer;-webkit-appearance: none;border-radius: 0px;-webkit-border-radius: 0px;
}

/* ********* ALERT MESSAGE ******** */
.alertmsg_div{
  position: absolute;top: -65px;left: 50%;background: $secondary-color;color: $primary-color;transform: translateX(-50%);padding: 10px;
}
.alertmsg_div_show{
  top: 65px !important;transition: all 0.25s ease-in;
}
.alertmsg_div .alertmsg_content{
  font-weight: bold;font-size: 14px;
}

/* ********** POPUP ************ */
.popup_div{
  position: absolute;top: 0px;left: 0px;right: 0px;bottom: 0px;background: $popup-background;color: $secondary-color;display: none;z-index:1;
}
.popup_div_show{
  display: inline-block !important;
}
.popup_close{
  position: relative;width: 100%;height: 20px;padding: 10px 0px;
}
.popup_close_icon{      
  font-size: 16px;position: relative;right: 10px;float: right;font-weight: bold;cursor: pointer;
}
.popup_contents{
  display: none;width: 100%;height: calc(100% - 42px);min-width: 320px;position: relative;margin: 0 auto;padding: 10px;box-sizing: border-box;
}
.popup_contents_show{
  display: inline-block !important;
}
.popup_contents > div{
  max-width: 500px;position: relative;margin: 0 auto;  
}
.popup_title{
  text-align: left;font-size: 16px;border-bottom: 3px solid;padding-bottom: 5px;font-weight: bold;
}
.popup_settings_row{
  width: 100%;float: left;padding: 10px 0px;border-bottom: 1px solid $settings-row-border;
}
.popup_settings_col{
  float: right;
}
.popup_settings_col_text{
    float: left;font-size: 14px;margin-top: 6px;
}

/* ********** Settings ********** */

/* Toggle Button */
.switch {
  position: relative;display: inline-block;width: 60px;height: 34px;
}
.switch input { 
  opacity: 0;width: 0;height: 0;
}
.slider {
  position: absolute;cursor: pointer;top: 0;left: 0;right: 0;bottom: 0;background-color: #ccc;-webkit-transition: .4s;transition: .4s;
}
.slider:before {
  position: absolute;content: "";height: 26px;width: 26px;left: 4px;bottom: 4px;background-color: white;-webkit-transition: .4s;transition: .4s;
}
input:checked + .slider {
  background-color: #2196F3;
}
input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}
input:checked + .slider:before {
  -webkit-transform: translateX(26px);-ms-transform: translateX(26px);transform: translateX(26px);
}
.slider.round {
  border-radius: 34px;
}
.slider.round:before {
  border-radius: 50%;
}